.App {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.dot {
  background-color: lightblue;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  width: 20px;
  height: 20px;
  border-radius: 10px;
}
.dot:focus {
  background-color: red !important;
}

.dot {
  animation: blinker 2s linear infinite;
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}
